/* SmartWizard Theme: Dots */

.sw-theme-dots {
  .sw-container {
    min-height: 300px;
  }

  .step-content {
    padding: 15px;
    border: none;
    background-color: #fff;
    text-align: left;
  }

  .sw-toolbar {
    background: #fff;
    border-radius: 0 !important;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 0 !important;
  }

  .sw-toolbar-top {
    border-bottom-color: #ddd !important;
  }

  .sw-toolbar-bottom {
    border-top-color: #ddd !important;
    border-bottom-color: #ddd !important;
  }

  > ul.step-anchor {
    position: relative;
    background: #fff;
    border: 0px solid #313e6a !important;
    list-style: none;
    z-index: 5;

    &:before {
      content: " ";
      position: absolute;
      top: 59px;
      bottom: 0;
      left: 20px;
      width: 96%;
      height: 5px;
      background-color: #ebeff8;
      border-radius: 3px;
      z-order: 0;
      z-index: 9;
    }

    > li {
      border: none;

      > a {
        position: relative;
        text-align: center;
        font-weight: bold;
        background: transparent;
        border: none;

        /* color: #313e6a; */
        text-decoration: none;
        outline-style: none;
        z-index: 9;
        display: block;
      }
    }
  }
}

/* Anchors styles */

.step-anchor .nav-tabs .nav-item .nav-link {
  color: #313e6a !important;
}

.sw-theme-dots {
  > ul.step-anchor > li > a:before {
    content: " ";
    position: absolute;
    bottom: 14px;
    left: 37%;
    margin-top: 20px;
    display: block;
    border-radius: 50%;
    color: #6259ca;
    background: #ebeff8;
    border: none;
    width: 30px;
    height: 30px;
    text-decoration: none;
    z-index: 98;
  }

  nav-tabs .nav-link:hover:not(.disabled) {
    color: inherit;
    background: transparent !important;
  }

  > ul.step-anchor > li {
    > a {
      &:after {
        content: " ";
        position: relative;
        left: 35%;
        bottom: 5px;
        margin-top: 24px;
        display: block;
        width: 16px;
        height: 16px;
        background: #b4bdce;
        border-radius: 50%;
        z-index: 99;
      }

      &:hover {
        color: #313e6a;
        background: transparent;
      }

      &:focus {
        color: #313e6a;
        border: none;
      }
    }

    &.clickable > a:hover {
      color: #999;
    }

    &.active > a {
      color: #6259ca;

      &:hover {
        border: none;
      }

      &:after {
        background: #6259ca;
      }
    }

    &.done > a {
      color: #313e6a;

      &:after {
        background: #b3b7da;
      }
    }

    &.danger > a {
      color: #d5cef3;

      &:after {
        background: #d5cef3;
      }
    }

    &.disabled > a {
      color: #eee !important;

      &:hover {
        color: #eee !important;
      }

      &:after {
        background: #eee;
      }
    }
  }
}

