/* Responsive CSS */
@media screen and (max-width: 480px) {
  .sw-theme-dots {
    > ul.step-anchor {
      &:before {
        top: 0;
        bottom: 0;
        left: 19px;
        width: 5px;
        height: 100%;
        background-color: #f5f5f5;
        display: block;
        margin-right: 10px;
      }

      > li {
        margin-left: 20px;
        display: block;
        clear: both;

        > a {
          text-align: left;
          margin-left: 0;
          display: block;

          &:before {
            top: -9px;
            left: -14px;
            margin-right: 10px;
            display: block;
          }

          &:after {
            top: -42px;
            left: -36px;
            margin-right: 10px;
            display: block;
          }
        }
      }
    }

    .nav .nav-item {
      width: 100%;
    }
  }
}
.step-anchor {
  .nav-link:focus {
    color: #313e6a !important;
  }
}